import React from 'react';
import '../../../index.scss';
import { IoLogoGithub } from "react-icons/io";
import { IoIosLink } from "react-icons/io";
import { IoLogoSass } from "react-icons/io";

import { IoLogoHtml5 } from "react-icons/io";
import { IoLogoCss3 } from "react-icons/io";
import { DiReact } from "react-icons/di";
import { DiGithubFull } from "react-icons/di";
import { DiNpm } from "react-icons/di";

import { FaNode } from "react-icons/fa";
import { DiMysql } from "react-icons/di";
import { IoLogoGameControllerB } from "react-icons/io";
import { FaHeadphonesAlt } from "react-icons/fa";
import { IoMdBicycle } from "react-icons/io";


const Item = ({title,text,image,linkHub,link,stack}) => {
    function get(){
        let arr= []
        for (let i=0;i<stack.length; i++) {
            arr.push(stack[i])
           
         }
         return (
             arr.map(el=>(
                <div>{el}</div> 
             ) )
         )
    }


    return (
    <div className="project-item">
    
        <div className="overlay" >
         <div className="image" style={{backgroundImage: `url(${image})` }}/>
        </div>
       
         <div className="icons">
            <div className="icon"> <a href={`${linkHub}`} target="_blank" rel="noopener noreferrer"> <IoLogoGithub /> </a> </div>
            <div className="icon"> <a href={`${link}`} target="_blank" rel="noopener noreferrer"> <IoIosLink /> </a> </div>
        </div>
        
       
        <div className="description">
            <h3>{title}</h3>
            <p>{text}</p>
        </div>
        <div className="tech-stack"> 
           {get()} 
        </div>
    </div>
    )
}

export default Item;